import React from 'react';
import { RootState } from '../store';
import { Store } from 'redux';

import { Apps } from '../lib/apps';
import BuyDomainsApp from '@zero-tech/buy-domains-zapp/src';
import { StakingZApp } from '@zero-tech/zapp-staking/src';
import { DaosApp } from '@zero-tech/zapp-daos/src';
import { Chains } from '../lib/web3';
import { ethers } from 'ethers';
import { Channels } from '../platform-apps/channels';
import { PlatformUser } from './container';

import './styles.scss';

export interface AppInterface {
  provider: ethers.providers.Web3Provider;
  route: string;
  web3: {
    chainId: Chains;
    address: string;
    connectWallet;
  };
}

export interface Properties {
  web3Provider: ethers.providers.Web3Provider;
  store: Store<RootState>;
  user?: PlatformUser;
  znsRoute: string;
  address: string;
  chainId: Chains;
  selectedApp: Apps;
  connectWallet: () => void;
}

export class AppSandbox extends React.Component<Properties> {
  get appProperties() {
    const { znsRoute, web3Provider, address, chainId, user, connectWallet } = this.props;

    return {
      route: znsRoute,
      provider: web3Provider,
      user,
      web3: {
        address,
        chainId,
        connectWallet
      },
    };
  }

  renderSelectedApp() {
    const { selectedApp, store } = this.props;

    if (selectedApp === Apps.BuyDomains) {
      return <BuyDomainsApp {...this.appProperties} />;
    }

    if (selectedApp === Apps.Staking) {
      return <StakingZApp {...this.appProperties} />;
    }

    if (selectedApp === Apps.DAOS) {
      return <DaosApp {...this.appProperties} />;
    }

    if (selectedApp === Apps.Channels) {
      return (
        <Channels
          {...this.appProperties}
          store={store}
        />
      );
    }

    return <div className='app-sandbox__error'>Error {selectedApp} application has not been implemented.</div>;
  }

  render() {
    return <div className='app-sandbox'>{this.renderSelectedApp()}</div>;
  }
}
